<template>
    <div>
        <h1>课程管理</h1>
        <el-divider></el-divider>
        <el-button-group class="margin_top_10 margin_bottom_10">
            <el-button type="primary" @click="handleAdd()">添加</el-button>
        </el-button-group>
        <el-divider></el-divider>
        <el-table
                cell-class-name="center"
                :data="list"
                border
                v-loading="tableLoading"
                header-cell-class-name="cell_class_name"
                style="width: 100%;margin-top: 20px">
            <el-table-column
                    align="center"
                    type="index"
                    label="序号"
                    :width="tableIndexWidth">
            </el-table-column>
            <el-table-column
                    label="封面图"
                    width="280">
                <template slot-scope="scope">
                    <el-image
                            fit="cover"
                            style="height: 80px;"
                            :src="getFileUrl(scope.row.coverUrl)"
                            :preview-src-list="[getFileUrl(scope.row.coverUrl)]">
                    </el-image>
                </template>
            </el-table-column>
            <el-table-column
                    align="center"
                    prop="title"
                    label="标题"
            >

            </el-table-column>
            <el-table-column
                    align="center"
                    prop="coachName"
                    label="教练"
            >

            </el-table-column>
            <el-table-column
                    align="center"
                    prop="rate"
                    label="难度"
            >

            </el-table-column>
            <el-table-column
                    align="center"
                    prop="times"
                    label="课程时间"
            >

            </el-table-column>
            <el-table-column
                    align="center"
                    prop="price"
                    label="课程价格"
            >

            </el-table-column>
            <el-table-column
                    align="center"
                    prop="peoples"
                    label="课程容量"
            >

            </el-table-column>
            <el-table-column
                    align="center"
                    :width="tableTimeWidth"
                    label="开课时间">
                <template slot-scope="scope">
                    <p>{{scope.row.startTimeStr}} - {{scope.row.endTimeStr}}</p>
                </template>
            </el-table-column>
            <el-table-column
                    label="操作"
                    width="150">
                <template slot-scope="scope">
                    <el-button-group>
                        <el-button type="primary" size="mini" @click="handleEdit(scope.row)">编辑</el-button>
                        <el-button type="danger" size="mini" @click="handleDelete(scope.row)">删除</el-button>
                    </el-button-group>
                </template>
            </el-table-column>
        </el-table>
        <Pagination
                :page-index.sync="queryBean.pageIndex"
                :page-size.sync="queryBean.pageSize"
                @refresh="refresh"
                :items-total="total"
        >

        </Pagination>
        <ClassEditor
                @refresh="refresh"
                ref="ClassEditor"
        >

        </ClassEditor>
        <DangerOperationConfigDialog
                danger
                ref="del_config"
                msg="是否删除选中的内容?"
                @configDone="deleteContent"
                button-name="删除"
        />
    </div>
</template>

<script>
    import Pagination from "../views/Pagination";
    import DangerOperationConfigDialog from "../views/DangerOperationConfigDialog";
    import ClassEditor from "@/views/ClassEditor";

    export default {
        name: "ClassManager",
        components: {ClassEditor, DangerOperationConfigDialog, Pagination},
        data() {
            return {
                tableLoading: false,
                list: [],
                queryBean: {
                    pageIndex: 0,
                    pageSize: 10
                },
                total: 0,
                delete_id: -1
            }
        },
        created() {
            this.refresh();
        },
        methods: {
            handleDelete: function (bean) {
                this.delete_id = bean.id;
                this.$refs.del_config.show();
            },
            handleAdd: function () {
                this.$refs.ClassEditor.show({
                    coverUrl: ''
                });
            },
            handleEdit: function (bean) {
                this.$refs.ClassEditor.show(bean);
            },
            refresh: function () {
                this.getList();
            },
            getList: function () {
                var that = this;
                this.requestList('/class_api/class', this.queryBean, function (total, list) {
                    that.tableLoading = false;
                    that.list = list;
                    that.total = total;
                })
            },
            deleteContent: function () {
                var that = this;
                this.requestDelete('/class_api/class/' + this.delete_id, function (res) {
                    that.handleInfo(res, '删除成功!');
                    that.refresh();
                })
            },
            changeState: function (bean, state) {
                var that = this;
                this.requestPut('/class_api/class', {
                    id: bean.id,
                    enable: state
                }, function (res) {
                    that.handleInfo(res, '修改状态成功!');
                    that.refresh();
                })
            }
        }
    }
</script>

