export default {
    install(Vue) {


        /**
         * 判断字符串是否可用
         * @param str
         * @returns {boolean}
         */
        Vue.prototype.strError = function (str) {
            if (str == null) {
                return true;
            }
            if (str == '') {
                return true;
            }
            if (str == undefined) {
                return true;
            }
            if (str.toString().replace(/\s+/g, '').length == 0) {
                return true;
            }
            return false;
        };

        /**
         * 浅赋值
         * @param bean
         */
        Vue.prototype.assignValue = function (bean) {
            return JSON.parse(JSON.stringify(bean));
        };

    }
}
