        /**
         *
         * 是否可以访问报账管理
         */
        Vue.prototype.canAccount = function () {
            var user = this.$store.state.user;
            if (user.admin) {
                return true;
            }
            var permissions = user.permissions;
            if (permissions.indexOf('account') !== -1) {
                return true;
            }
            return false;
        };
