        /**
         *
         * 返回当前年份
         */
        Vue.prototype.appendDurationTime = function (year, month, day) {
            var result = '';
            if (year > 0) {
                result += year + ' 年 ';
            }
            if (month > 0) {
                result += month + ' 月 ';
            }
            if (day > 0) {
                result += day + ' 天 ';
            }
            return result;
        };

        /**
         *
         * 返回当前年份
         */
        Vue.prototype.convertMoneySource = function (type) {
            if (type == null) {
                return '未知';
            }
            return this.moneySource[type].label;
        };

