        // 获取建议等级 奖种类型 评审等级
        Vue.prototype.getSuggestLevel = function (awardType, level) {
            if (level == 0) {
                return '未投票';
            }
            switch (awardType) {
                case 1:
                    switch (level) {
                        case 1:
                            return '一等奖';
                        case 2:
                            return '二等奖';
                        case 3:
                            return '三等奖';
                        case 4:
                            return '不授';
                    }
                    return;
                case 2:
                    switch (level) {
                        case 1:
                            return '授奖';
                        case 4:
                            return '不授';
                    }
                    return;
            }
        };

        // 返回当前奖种
        Vue.prototype.returnAward = function (type) {
            switch (type) {
                case 1:
                    return '专利奖';
                case 2:
                    return '发明人奖';
            }
            return '专利奖';
        };

        // 返回奖种列表
        Vue.prototype.returnAwards = function () {
            var parts = [
                {
                    value: 1,
                    label: '专利奖'
                }, {
                    value: 2,
                    label: '专利发明人奖'
                }
            ];
            return parts;
        };

