export default {
    install(Vue) {

        /**
         * 转换字符串到列表
         * @param str
         */
        Vue.prototype.formatDouble = function (value, size) {
            if (!value) {
                return '0';
            }
            return value.toFixed(size);
        };
        /**
         * 转换字符串到列表
         * @param str
         */
        Vue.prototype.formatDouble2 = function (value) {
            if (!value) {
                return '0';
            }
            return parseFloat(value.toFixed(2));
        };

    }
};
